* {
    box-sizing: border-box;
}

body { margin: 0; background-color: #f1f1f1; }

.bg {
    margin: 0 auto; text-align: center;
}

#cunt {
    width: 960px; height: 602px; display: block; margin: 0 auto; background-color: #ffffff; position: relative;

    img { opacity: 0; width: 100%; }

    canvas {
